.card{
    
    border-radius: 10px;
    overflow: hidden;
    border: 3px solid;
    display: flex;
    flex-direction:  column;
    min-width: 200px;
    margin: 10px;
}


.card .title{
    padding: 5px 10px;
    color: black;
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    font-weight: bold;


    
}
.card .conteudo{
    background-color: white;
    color: black;
    padding: 10px;
    flex: 1;/*cresce conte√∫do*/
    margin: 1px;
}